'use client';

import { useEffect } from 'react';
import { usePathname } from 'next/navigation';

export default function AOSInit() {
	const pathname = usePathname();

	useEffect(() => {
		function initAOS() {
			const win: any = window as any;
			if (win && win.AOS && typeof win.AOS.init === 'function') {
				// Initialize AOS with better settings for client-side navigation
				win.AOS.init({ 
					duration: 1200,
					once: false, // Allow animations to repeat on navigation
					offset: 100,
					delay: 0
				});
				
				// Try to refresh a few times to ensure elements in late-loaded sections animate
				try { win.AOS.refreshHard && win.AOS.refreshHard(); } catch {}
				setTimeout(() => { try { win.AOS.refresh && win.AOS.refresh(); } catch {} }, 200);
				setTimeout(() => { try { win.AOS.refreshHard && win.AOS.refreshHard(); } catch {} }, 600);
				return true;
			}
			return false;
		}

		// Try immediately
		if (!initAOS()) {
			// Retry a few times in case script loads slightly later
			let attempts = 0;
			const timer = setInterval(() => {
				attempts++;
				if (initAOS() || attempts > 10) {
					clearInterval(timer);
				}
			}, 200);
			return () => clearInterval(timer);
		}
	}, [pathname]); // Re-run when pathname changes

	return null;
}


